body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
  background: #333
}

html {
  font-size: 62.5%;
}
.App {
  background: #1F1F1F;
  text-align: center;
  color: #DDDDDD;
  min-width: 300px;
  width: 40%;
  height: 90vh;
  margin: 5vh auto;
  border: 1px solid #61DAF7;
  overflow: hidden;
}

@media (max-width: 1200px) {
  .App {
    width: 50%;
  }
}
.header {
   margin: 0 auto;
}

.header__title {
   border-bottom: 1px solid #61DAF7;
   font-size: 4rem;
   letter-spacing: .2rem;
   line-height: 15rem;
}

.header__form {
   display: -webkit-flex;
   display: flex;
   -webkit-flex-wrap: wrap;
           flex-wrap: wrap;
   -webkit-justify-content: space-between;
           justify-content: space-between;
   -webkit-align-items: flex-start;
           align-items: flex-start;
   height: 10rem;
   width: 70%;
   padding: 0 2rem;
   margin: 2rem auto;
   border-bottom: 1px solid #61DAF7;
}

.header__input {
   -webkit-flex-grow: 1;
           flex-grow: 1;
   background: #DDD;
   height: 4rem;
   padding: 0 1.5rem;
   border: none;
   font-size: 1.6rem;
}

.header__icon {
   margin: 0 .7rem;
   font-size: 3rem;
   line-height: 4rem;
   cursor: pointer;
   -webkit-transition: .2s;
   transition: .2s;
}

.header__icon:hover {
   color: #61DAF7;
   -webkit-transform: scale(1.2);
           transform: scale(1.2)
}

.header__warning-info {
   font-size: 1.8rem;
}
.tasksList {
   margin: 0 auto;
}

.tasksList .tasksToDo {
   height: 35rem;
   overflow: auto;
}

.tasksList .tasksToDo__title {
   margin: 1.5rem auto;
   font-size: 3.5rem;
}

.tasksList .tasksToDo__list {
   padding: 0;
   list-style: none;
   letter-spacing: .2rem;
}

.tasksList .tasksToDo__item {
   width: 60%;
   font-size: 2.5rem;
   padding-bottom: 1.2rem;
   margin-bottom: 2rem;
   border-bottom: 1px dashed #61DAF7;
}

.tasksList .tasksToDo__icon {
   margin-left: 5rem;
   cursor: pointer;
   -webkit-transition: .2s;
   transition: .2s;
}

.tasksList .tasksToDo__icon:hover {
   color: #61DAF7;
   -webkit-transform: scale(1.2);
           transform: scale(1.2)
}
